@font-face {
    font-family: 'droid-serif';
    src: url('fonts/DroidSerif-Regular.ttf');
}

$bg-color: #f5f5f5;
$text-color: #0f0f0f;
$accent-color: #b89d4f;

$title-font: 'Playfair Display', serif;
$sub-font: 'Poppins', sans-serif;

* {
    -ms-overflow-style: none;
    user-select: none;
    box-sizing: border-box;
}

p,
a,
li {
    user-select: text;
}

html::-webkit-scrollbar {
    display: none;
}

a {
    text-decoration: none;
    color: unset;
}

html,
body {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    color: #2f2f2f;
    background-color: $bg-color;
}

.title {
    text-align: center;
    font-weight: 400;
    font-family: $title-font;
    color: $text-color;
    letter-spacing: -4px;
    font-size: 60px;
}

#content {
    z-index: -100;
    opacity: 0;
    height: 100%;
    background-color: $bg-color;
}

#navbar {
    display: inline-flex;
    width: 100vw;
    height: 80px;
    background-color: $bg-color;
}

#menu {
    width: calc(100vw - 160px);
    justify-content: flex-end;
    display: inline-flex;
    margin-right: 10vw;
    align-items: center;
}

#menu-txt {
    font-size: 14px;
    font-family: 'Droid-serif';
    margin-right: 5px;
}

#list-ico {
    transform: scale(0.8);
}

.dropdown-menu:hover {
    cursor: pointer;
}

#title {
    width: 100vw;
    height: auto;
    display: inline-flex;
    font-family: $title-font;
    font-weight: 700;
    font-size: min(35px, 7vw);
    margin-left: 10vw;
    align-items: center;
}

#title p {
    user-select: none;
}

#subnav {
    width: 100vw;
    position: absolute;
    top: 90px;
}

/* srodek */
#subnav {
    width: 100vw;
    text-align: center;
    right: 0;
}

/* z prawej */
#subnav {
    width: unset;
    text-align: right;
    right: 10vw;
    display: none;
}

.menu-item:first-child {
    margin-top: 10px;
}

.menu-item:before {
    content: '';
    position: absolute;
    width: 0;
    height: 1px;
    bottom: 0;
    right: 0;
    background-color: #9f9f9f;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
}

.menu-item:hover:before {
    visibility: visible;
    width: 100%;
}

.menu-item {
    position: relative;
    font-size: 12px;
    margin-bottom: 10px;
    font-family: $sub-font;
    font-weight: 200;
    right: -100px;
    display: table;
    float: right;
    clear: right;
    cursor: pointer;
    text-transform: uppercase;
}

#flags {
    position: absolute;
    top: -25px;
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    width: 90px;
    margin-right: -5px;
    right: -130px;
}

.flag {
    position: relative;
    margin: 5px;
    width: 20px;
    cursor: pointer;
}

#flags:hover .flag {
    opacity: 0.5;
}

#flags:hover .flag:hover {
    opacity: 1;
}

#container {
    display: flex;
    align-items: center;
    flex-direction: column;
}

#intro {
    width: 100vw;
    height: calc(100vh - 80px);
    display: flex;
    justify-content: center;
}

#intro-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    margin-top: 5vh;
}

#intro p {
    margin: unset;
}

#intro-img {
    width: 70%;
    max-width: 100%;
    min-width: 300px;
    flex-shrink: 0;
    margin-top: 15vh;
    background-color: transparent;
    box-shadow: 1.25rem 1.375rem 4.75rem 0 rgba(0, 0, 0, 0.42) !important;
}

#author {
    margin-top: 50px;
    font-size: 10vw;
}

#bounce-arrow {
    position: absolute;
    width: 40px;
    height: 40px;
    opacity: 1;
    visibility: visible;
    cursor: pointer;
    flex-grow: 0;
    bottom: 0;
    animation: bounce 3s infinite;
}

#bounce-arrow > svg {
    width: 40px;
    height: 40px;
    opacity: 70%;
    fill: $accent-color;
}

.separator:last-child {
    border-left: 1px solid $accent-color;
}

.separators {
    display: flex;
    padding: 10vw 0;
}

.separator {
    width: 50vw;
    height: 100px;
    display: inline-block;
}

#collection {
    width: 100%;
    align-items: center;
    padding-bottom: calc(10vh - 20px);
    display: flex;
    flex-direction: column;
}

#preview-wrap {
    width: 100vw;
    display: flex;
    align-items: center;
    flex-direction: column;
}

#collection-preview {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 2.5vw 0;
}

.preview-pic {
    width: 45%;
    height: 30vw;
    border: 1px solid #dbdbdb;
    border-radius: 10px;
    margin: 1.25vw;
    cursor: pointer;
}

#collection-full {
    width: 170px;
    height: 35px;

    display: flex;
    justify-content: center;
    align-items: center;

    margin: 20px 0;
    border: 1px solid $accent-color;
    cursor: pointer;

    color: $accent-color;
    font-family: $sub-font;
    font-weight: 100;
    font-size: 15px;
    text-transform: uppercase;

    transition: 0.5s all;
}

#collection-full:hover {
    background-color: $accent-color;
    color: white;
}

#about-me {
    width: 100vw;
    font-family: $sub-font;
    font-weight: 200;
    padding: 0 5vw;
    background-color: $bg-color;
}

#about-me img:first-child {
    /* for vertical photo */
    /* width: 80vw; */
    /* margin: 0 5vw 3vh 5vw; */ /* margin 5vw because #about-me has 5vw */
    width: 15vw;
    min-width: 220px;
    padding: 0 3vw 0 0; /* padding 5vw because #about-me has 5vw */
}

#about-me p:nth-child(n + 2) {
    font-size: 18px;
    text-align: justify;
    text-indent: 40px;
}

.p-separators {
    padding: 2vh 0;
}

.p-separator {
    width: 50vw;
    height: 30px;
    display: inline-block;
}

#about-me .title {
    text-indent: 0px;
}

#web-contents {
    padding-bottom: 20vh;
}

#web-contents-txt {
    margin-bottom: 100px;
}

.web-link {
    margin-bottom: 150px;
    display: flex;
    flex-direction: column;
    padding: 0 30px;
}

.web-img {
    object-fit: cover;
    margin-top: 20px;
    width: 90vw;
    height: 90vw;
    box-shadow: 1.25rem 1.375rem 4.75rem 0 rgba(0, 0, 0, 0.42) !important;
}

.web-header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.web-date {
    color: $accent-color;
    display: inline;
    font-family: $sub-font;
    text-transform: uppercase;
    /* user-select: text; */
}

.web-date-line {
    height: 50%;
    border-top: 1px solid $accent-color;
    width: 150px;
    margin-left: 20px;
}

.web-title h4 {
    font-family: $title-font;
    letter-spacing: -0.05em;
    font-weight: normal;
    font-size: min(5vw, 30px);
}

/* -----------> DESKTOP <------------ */
@media only screen and (min-width: 750px) {
    #title {
        width: unset;
        margin-left: 15vw;
        margin-top: -5px;
    }

    #title-text {
        font-size: min(35px, 3.5vw);
    }

    .title {
        font-size: 80px;
    }

    #navbar {
        height: 150px;
        justify-content: space-between;
    }

    #menu {
        width: unset;
        margin-right: 15vw;
    }

    #menu-txt,
    #list-ico {
        display: none;
    }

    #subnav {
        position: unset;
        display: inline-block;
        right: unset;
        top: unset;
    }

    .menu-item:first-child {
        margin-top: unset;
    }

    .menu-item {
        display: inline-block;
        right: 0;
        float: none;
        margin-right: calc(2vw + 5px);
        margin-bottom: unset;
        font-size: 14px;
    }

    .menu-item:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 1px;
        bottom: 0;
        left: 0;
        background-color: #9f9f9f;
        visibility: hidden;
        transform: scaleX(0);
        transition: all 0.3s ease-in-out;
    }

    .menu-item:hover:before {
        visibility: visible;
        transform: scaleX(1);
    }

    #flags {
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        width: 20px;
        top: unset;
        right: unset;
    }

    .flag {
        margin: 4.5px 0;
        width: 20px;
        cursor: pointer;
    }

    .flag:not(:first-child) {
        display: none;
    }

    #flags:hover .flag {
        display: block;
        opacity: 0.5;
        margin-top: 4.5px;
    }

    #flags:hover .flag:hover {
        opacity: 1;
    }

    #container {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    #intro {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: calc(100vh - 150px);
    }

    #intro-content {
        margin-top: 0;
        justify-content: center;
        align-items: center;
        width: 100vw;
    }

    #intro-img {
        width: 45vw;
        margin-top: 3vh;
        margin-bottom: 15vh;
        margin-left: 30vw;
    }

    #author {
        text-align: left;
        right: 52vw;
        margin-top: -12vh;
        position: absolute;
        z-index: 2;
        font-size: min(4.5vw, 80px);
        display: inline-block;
    }

    #author p:last-child {
        margin-left: 3vw;
        font-size: 0.8em;
    }

    .separators {
        padding: 5vh 0;
    }

    #collection {
        width: 100vw;
    }

    #collection-preview {
        max-width: 1000px;
    }

    .preview-pic {
        width: 30%;
        height: auto;
        margin: 0.5vw;
    }

    #about-me {
        max-width: 1000px;
    }

    #about-me img:first-child {
        /* for vertical photo */
        /* width: 60vw; */
        /* max-width: 500px; */
        margin: 7px 20px 5px 0;
    }

    .p-separators {
        padding: 2vh 0;
    }

    #web-contents {
        width: 75vw;
        max-width: 1110px;
    }

    .web-link {
        padding: 0;
        flex-direction: row;
        margin-top: 150px;
    }

    .web-text {
        padding: 2vw 7vw 40px 7vw;
        width: 45vw;
    }

    .web-date-line {
        width: min(150px, 12.5vw);
    }

    .web-link:nth-child(odd) {
        flex-direction: row-reverse;
    }

    .web-link:nth-child(even) .web-text {
        padding-left: 0;
    }

    .web-title h4 {
        letter-spacing: -0.05em;
        font-weight: normal;
        font-size: min(3.5vw, 35px);
        margin-top: 2vw;
    }

    .web-img {
        margin-top: 0;
        width: 30vw;
        height: 30vw;
        max-height: 450px;
        max-width: 450px;
    }
}

@keyframes bounce {
    0%,
    20%,
    50%,
    80%,
    100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-10px);
    }
    60% {
        transform: translateY(-5px);
    }
}
